.App {
  text-align: center;
}
  .App .container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column
  }
  .App .container.sign_up,.App .container.login {
  max-width: 500px;
  width:100%;
  margin: 1.5em auto 0;
  padding: 0 .5em;
}
  .App .container.sign_up input,.App .container.sign_up button,.App .container.login input,.App .container.login button {
        margin:.8em 0;
}
  .App .container header,.App .container main {
      min-width: 300px;
}
*{-webkit-box-sizing: border-box;box-sizing: border-box;margin:0;padding: 0; }
li,ul,ol{list-style: none;}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #FDFDFD;
}
.ant-empty {
  margin: 8px;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



