#home{
  max-width: 960px;
  padding: 16px;
  width: 100%;
  margin: 0 auto;
}
  #home header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    border-bottom:1px solid #e1e1e1;
    margin-bottom: 20px;
  }
  #home header h1{
      font-size: 16px;
      font-weight: 450;
      margin:0;
    }
  #home header .userInfo{
      margin: 0 16px 0 auto;
    }
  #home header .userInfo .userName{
        margin-right: .3em;
      }
  #home header .logo{
      height: 35px;
      margin-left: 16px;
    }
  #home main{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 100%;
  }
  #home main .content{
      background: #fff;
      -webkit-box-shadow: 0 2px 0 rgba(225,225,225,.2);
              box-shadow: 0 2px 0 rgba(225,225,225,.2);
      border: 1px solid #ddd;
      border-radius: 8px;
      width: calc(50% - 8px);
    }

@media (max-width:760px){
    #home main{
      -ms-flex-direction: column;
          flex-direction: column;
    }
      #home main .content{
        width: 100%;
        margin-bottom: 16px;
      }
}



